import React, { useState, useEffect} from "react";
import { useUser } from "../utils/UserContext";
import { useNavigate } from 'react-router-dom';
import { getCSRFToken, setCSRFToken } from '../utils/csrf';
import routes from '../utils/urls';
import './authorization.css';

const Authorization = () => {
    const navigate = useNavigate();
    const { setUser } = useUser();
    const [errorMessage, setErrorMessage] = useState('');

    const handleSubmit = async (event) => {
        event.preventDefault();

        const username = event.target.username.value.trim();
        const password = event.target.password.value.trim();

        if (!username || !password) {
            setErrorMessage('      ');
            setTimeout(() => setErrorMessage(''), 5000);
            return;
        }

        const csrfToken = getCSRFToken();
        console.log('CSRF Token:', csrfToken);

        try {
            const response = await fetch(routes.login, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'X-CSRFToken': csrfToken,
                },
                credentials: 'include',
                body: JSON.stringify({
                    username: username,
                    password: password,
                }),
            });

            if (!response.ok) {
                console.log('Form submission response:', response);
                const text = await response.text();
                console.error(' :', text);
                throw new Error('   ');
            }

            const data = await response.json();
            if (data.success) {
                if (data.csrfToken) {
                    setCSRFToken(data.csrfToken);
                }
                setUser(data.user);

                console.log(data);
                navigate(routes.main);

            } else {
                setErrorMessage('   ');
                setTimeout(() => setErrorMessage(''), 5000);
            }
        } catch (error) {
            setErrorMessage('   ');
            setTimeout(() => setErrorMessage(''), 5000);
        }

    };

    return (
        <div className="auth center">
            <div className="auth__background">
                <div className="auth__modal">
                    <div className="auth__modal-header">
                        <h2 className="auth__modal-header_logo"> </h2>
                    </div>
                    <div className="auth__modal-body">
                        <h2 className="auth__modal-body_head">   </h2>
                        <form method='POST' className="auth__form" onSubmit={handleSubmit}>
                            <h3 className="auth__form_lable"></h3>
                            <input name="username" type="login" className="auth__input" placeholder="User"/>
                            <h3 className="auth__form_lable"></h3>
                            <input name="password" type="password" className="auth__input" placeholder="Password"/>
                            <button type="submit" className="auth__submit"></button>
                        </form>
                        <div className="auth__forgot-password">
                            <a href="#"> /
                                 </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    );
}
export default Authorization;